import React, { Component } from "react";
import * as S from "./PageDividerStyles";

class PageDivider extends Component {
  render() {
    const { pageDividerHeaderText, pageDividerText, children } = this.props;
    return (
      <S.Wrapper>
        <S.HeaderTitle>{pageDividerHeaderText}</S.HeaderTitle>
        <S.Description>{pageDividerText}</S.Description>
        <S.GridWrapper>{children}</S.GridWrapper>
      </S.Wrapper>
    );
  }
}

export default PageDivider;
