import React, { Component } from "react";
import { About, Job, Layout } from "../../components";
import * as IntrigueConstants from "../../assets/constants/intrigue-report";
import * as ecobeeConstants from "../../assets/constants/ecobee-report";
import * as S from "./WorkTermReportContainerStyles";

class WorkTermReportContainer extends Component {
  getConstants = () => {
    const { match } = this.props;
    const company = match.params.company;
    if (company === "intrigue-media") {
      return IntrigueConstants;
    }
    if (company === "ecobee") {
      return ecobeeConstants;
    }
    return null;
  };
  render() {
    const Constants = this.getConstants();

    return (
      <Layout>
        <S.Wrapper>
          <S.MainHeaderWrapper>
            <S.CompanyLogoWrapper>
              <img src={Constants.Logo} alt="" height="auto" width="100%" />
            </S.CompanyLogoWrapper>
            <S.BodyWrapper>
              <S.BodyItem>
                <About
                  questionOne={Constants.aboutQuestionOne}
                  questionTwo={Constants.aboutQuestionTwo}
                  answerOne={Constants.aboutAnswerOne}
                  answerTwo={Constants.aboutAnswerTwo}
                />
              </S.BodyItem>
              <S.BodyItem>
                <Job
                  questionOne={Constants.jobQuestionOne}
                  questionTwo={Constants.jobQuestionTwo}
                  questionThree={Constants.jobQuestionThree}
                  answerOne={Constants.jobAnswerOne}
                  answerTwo={Constants.jobAnswerTwo}
                  answerThree={Constants.jobAnswerThree}
                />
              </S.BodyItem>
            </S.BodyWrapper>
          </S.MainHeaderWrapper>
        </S.Wrapper>
      </Layout>
    );
  }
}

export default WorkTermReportContainer;
