import React from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import * as Routes from "../";

const AppContainer = () => {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Routes.HomeContainer} />
        <Route
          path="/work-term-report/:company"
          component={Routes.WorkTermReportContainer}
        />
      </Switch>
    </Router>
  );
};

export default AppContainer;
