import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { IoMdArrowDropdown } from "react-icons/io";
import "./DropDownMenu.css";

class DropDownMenu extends Component {
  state = {
    isOpen: false
  };
  showMenu = event => {
    event.preventDefault();

    this.setState({ isOpen: true }, () => {
      document.addEventListener("click", this.closeMenu);
    });
  };
  closeMenu = () => {
    this.setState({ isOpen: false }, () => {
      document.removeEventListener("click", this.closeMenu);
    });
  };
  render() {
    const { isOpen } = this.state;
    const { btnText } = this.props;
    return (
      <div className="drop-down-menu">
        <button onClick={this.showMenu} className="drop-down-menu__button">
          <span>{btnText}</span>
          <IoMdArrowDropdown size={32} color={"#fff"} />
        </button>
        {isOpen && (
          <div className="drop-down-menu__menu">
            <NavLink
              className="drop-down-menu__navlink"
              to="/work-term-report/ecobee"
            >
              <div className="drop-down-menu__menu__menu-item-wrapper">
                <span className="drop-down-menu__menu__menu-item">ecobee</span>
              </div>
            </NavLink>
            <NavLink
              className="drop-down-menu__navlink"
              to="/work-term-report/intrigue-media"
            >
              <div className="drop-down-menu__menu__menu-item-wrapper">
                <span className="drop-down-menu__menu__menu-item">
                  Intrigue Media
                </span>
              </div>
            </NavLink>
          </div>
        )}
      </div>
    );
  }
}

export default DropDownMenu;
