import React, { Component } from "react";
import { IconContext } from "react-icons";
import "./GridComponent.css";

class GridComponent extends Component {
  render() {
    const {
      gridTitle,
      Icon,
      heading,
      description,
      heading2,
      description2
    } = this.props;

    return (
      <div className="grid-component">
        <IconContext.Provider
          value={{
            color: "#ff4242",
            className: "grid-component__icons"
          }}
        >
          <div>{Icon}</div>
        </IconContext.Provider>
        <h1 className="grid-component__title">{gridTitle}</h1>
        <div className="grid-component__description">
          <span className="grid-component__heading">{heading}</span>
          <span className="grid-component__answer">{description}</span>
        </div>
        <div className="grid-component__description">
          <span className="grid-component__heading">{heading2}</span>
          <span className="grid-component__answer">{description2}</span>
        </div>
      </div>
    );
  }
}

export default GridComponent;
